.main {
  height: 100%;

  .content-wrapper {
    display: grid;
    grid-template-columns: 0.7fr 0.3fr;

    @media (max-width: 1100px) {
      display: flex;
      flex-direction: column;
      flex: 0 1 auto;
    }

    .content-main-block {
      padding-top: 100px;
      display: grid;
      grid-template-rows: 0.4fr 0.6fr;
      border: 2px solid #00000059;

      .table-body__tooltip {
        position: fixed;
        padding: 10px 20px;
        font-size: 1.2rem;
        border: 1px solid #b3c9ce;
        border-radius: 4px;
        text-align: center;
        font: italic 14px/1.3 sans-serif;
        color: #333;
        background: rgb(208, 222, 180);
        box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
      }

      .table-body__table {
        position: relative;
        height: 400px;
        margin-bottom: 40px;
        width: 100%;
        background-image: url('../../assets/table.png');
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: cover;

        .sandbox-body__plate {
          position: relative;
          content: '';
          content: url('../../assets/plate.png');
          left: 37px;
          top: 48px;
          width: 238px;
          height: 140px;
          cursor: pointer;
        }

        .sandbox-body__cup {
          position: absolute;
          content: url('../../assets/cup.png');
          left: 158px;
          top: 74px;
          width: 71px;
          height: 87px;
        }

        .sandbox-body__pear {
          position: absolute;
          content: url('../../assets/pear.png');
          left: 339px;
          top: 137px;
          width: 71px;
          height: 87px;
        }

        .sandbox-body__cherry {
          position: absolute;
          content: url('../../assets/cherry.png');
          left: 260px;
          top: 139px;
          width: 71px;
          height: 75px;
        }

        .sandbox-body__apple {
          position: absolute;
          content: url('../../assets/apple.png');
          left: 346px;
          top: 87px;
          width: 71px;
          height: 75px;
        }

        .sandbox-body__apple:hover {
          width: 66px;
          height: 70px;
        }

        .sandbox-body__pickle {
          position: absolute;
          content: url('../../assets/pickle.png');
          left: 175px;
          top: 74px;
          width: 26px;
          height: 47px;
        }
      }

      .sandbox-body {
        display: flex;
        width: 100%;
      }
    }
  }
}

.sandbox-body {
  display: flex;
  flex-direction: column;

  .sandbox-body__css {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 0px auto;
    width: 100%;
    border: 2px solid #00000059;


    .css-sandbox__title {
      margin-bottom: 20px;
      font-size: 1.4rem;
      color: red;
    }

    .css-sandbox__input-block {
      border-radius: 17px;
      display: flex;
      align-items: center;

      @media (max-width: 650px) {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
      }

      .css-sandbox__input {
        padding: 10px;
        font-size: 1.2rem;
        outline: none;
        width: 450px;
        height: 30px;
        margin-right: 20px;

        @media (max-width: 550px) {
          width: 400px;
        }
      }


      .css-sandbox__input__not-valid {
        border: 3px solid rgb(219, 97, 97);
      }

      .css-sandbox__button {
        font-size: 1.3rem;
        width: 100px;
        height: 30px;
        background-color: #f45757;
        border-radius: 5px;
        cursor: pointer;
        transition: all .5s;

        &:hover {
          opacity: 0.7;
        }
      }
    }

    .css-sandbox__buttons-block {
      display: flex;
      column-gap: 40px;
      padding: 20px 10px 20px 10px;

      .css-sandbox__button-help {
        font-size: 1.3rem;
        width: 100px;
        height: 30px;
        background-color: coral;
        border-radius: 5px;
        cursor: pointer;
        transition: all .5s;

        &:hover {
          opacity: 0.7;
        }
      }

      .css-sandbox__button-restart {
        font-size: 1.3rem;
        width: 100px;
        height: 30px;
        background-color: coral;
        border-radius: 5px;
        cursor: pointer;
        transition: all .5s;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }

  .sandbox-body__html {
    display: flex;
    height: 100%;

    .sandbox-body__numbers {
      font-size: 1.5rem;
      width: 6%;
      background-color: rgb(148 148 148);
      padding: 10px;

      @media (max-width: 750px) {
        width: 10%;
      }
    }


    .sandbox-body__editor {
      padding: 10px;
      font-size: 1.5rem;
      width: 97%;
      background-color: rgb(111 112 114);
      color: rgb(208, 222, 180);

      .editor__item {
        height: 20px;
        transition: all 0.3s;

        &:hover {
          color: yellow;
        }
      }
    }
  }
}

.sandbox-body__css__fall {
  transition: all 0.5s ease;
  animation: rotate 1s linear infinite;
}

.css-sandbox__input__failed {
  border: 3px solid red;
}

.finally-message {
  position: absolute;
  top: 15%;
  left: 30%;
  width: 40%;
  font-size: 1.7rem;
  color: aqua;
}
