.levels-block {
  border: 2px solid #00000059;

  .levels-block__title {
    color: #141514;
    font-size: 1.4rem;
    text-align: center;
    margin-bottom: 5px;
  }

  .levels-block__levels {
    .levels-block__item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      color: #141514;
      font-size: 1.2rem;
      text-align: center;
      cursor: pointer;
      padding: 10px;
      border: 1px solid black;

      .levels-block__item-title {
        color: #50622d;
        margin-bottom: 5px;
      }

      .levels-block__item-text {
        font-size: 1.4rem;
        margin-bottom: 10px;
        color: #141514;
      }

      .levels-block__level-status {
        display: flex;
        align-items: center;
        column-gap: 10px;

        .levels-block__level-title {
          font-size: 1.3rem;
        }

        .levels-block__level__uncomplete {
          content: url('../../assets/svg/uncomplete.svg');
          width: 30px;
          height: 30px;
        }

        .levels-block__level__complete {
          content: url('../../assets/svg/complete.svg');
          width: 30px;
          height: 30px;
        }

        .levels-block__level__help-complete {
          content: url('../../assets/svg/comple-with-help.svg');
          width: 30px;
          height: 30px;
        }
      }
    }

    .levels-block__item__active {
      padding: 10px;
      color: #141514;
      font-size: 1.2rem;
      text-align: center;
      cursor: pointer;
      border: 5px solid #c76529;

      .levels-block__item-title {
        color: #50622d;
      }

      .levels-block__item-text {
        color: #3d3d45;
      }
    }
  }
}
