.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 50px;
  column-gap: 60px;
  border: 2px solid #00000059;

  .footer__logo {
    width: 100px;
    height: 50px;
    transition: all 0.3s;

    &:hover {
      box-shadow: 3px 3px #1a1f20, -1em 0 .4em #1a1f20;
    }
  }

  .copyright {
    font-size: 1.6rem;
    color: #141514;
    text-align: center;
  }

  .copyright a {
    font-size: 1.6rem;
    font-weight: 800;
    color: #141514;
    transition: all 0.3s;
  }

  .copyright a:hover {
    opacity: .6;
  }

  @media (max-width: 400px) {
    footer {
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      column-gap: 1rem;
    }
  }

  @media (max-width: 350px) {
    footer .copyright {
      font-size: 1rem;
      color: #ffffffa3;
      text-align: center;
    }
    .footer__logo {
      width: 70px;
      height: 50px;
    }
  }
}


